header {
  padding: 20px 0px;
  position: sticky;
  top: 0px;
  z-index: 1;
  background-color: #bc5275;
  color: #fff2f1;
  border-bottom: solid;
  border-color: #b12b58;
  border-width: 10px;
}

nav {
  display: grid;
}

ul {
  padding: 10px 0px;
  margin: auto;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: stretch;
  text-align: center;
  grid-column-gap: 2rem;
}

a:link {
  color: #ffffff;
  text-decoration: none;
}

a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: #551ab8;
}

@media all and (max-width: 425px) {
  ul {
    grid-template-columns: repeat(3, 2fr);
  }
}

.section {
  padding: 50px 0px;
}

p {
  margin-bottom: 10px;
}

h1 {
  font-weight: bold;
  margin-bottom: 10px;
}

img {
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

h1 {
  font-weight: bold;
  margin-bottom: 20px;
}

h2 {
  margin-left: 2rem;
}

h3,
h5 {
  text-align: center;
}

p {
  margin-bottom: 10px;
}

nav {
  display: grid;
}

.ul {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.header {
  position: relative;
  background-color: #466ca5;
  color: #fff2f1;
  font-weight: bold;
}

.body-section {
  border-top-width: 100px;
  padding: 50px 0px;
  color: #fff2f1;
  background-color: #007acc;
}

.colorStyle {
  background-image: linear-gradient(
    rgba(177, 43, 88, 0.3) 0%,
    rgba(236, 102, 142, 0.6) 100%
  );
}

.mediaFlex {
  @media (max-width: 425px) {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}

input {
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}

.formLayer {
  padding: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  font-weight: bold;
}

.submit {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

